// .social-box
.social-box {
    min-height: 160px;
    margin-bottom: 20px;
    text-align: center;
    background: #fff;
    border: $card-border-width solid $card-border-color;
    @include border-radius($card-border-radius);

    i {
        display: block;
        margin: -1px -1px 0;
        font-size: 40px;
        line-height: 90px;
        background: $gray-lighter;

        @include border-radius($card-border-radius-inner $card-border-radius-inner 0 0);
    }

    .chart-wrapper {
        height: 90px;
        margin: -90px 0 0;

        canvas {
            width: 100% !important;
            height: 90px !important;
        }
    }

    ul {
        padding: 10px 0;
        list-style: none;


        li {
            display: block;
            float: left;
            width: 50%;

            &:first-child {
                border-right: 1px solid $border-color;
            }

            strong {
                display: block;
                font-size: 20px;
            }

            span {
                font-size: 10px;
                font-weight: 500;
                color: $border-color;
                text-transform: uppercase;
            }
        }
    }

    &.facebook {
        i {
            color: #fff;
            background: $facebook;
        }
    }

    &.twitter {
        i {
            color: #fff;
            background: $twitter;
        }
    }

    &.linkedin {
        i {
            color: #fff;
            background: $linkedin;
        }
    }

    &.google-plus {
        i {
            color: #fff;
            background: $google-plus;
        }
    }
}

.horizontal-bars {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
        position: relative;
        height: 40px;
        line-height: 40px;
        vertical-align: middle;

        .title {
            width: 100px;
            font-size: 12px;
            font-weight: 600;
            color: $text-muted;
            vertical-align: middle;
        }

        .bars {
            position: absolute;
            top: 15px;
            width: 100%;
            padding-left: 100px;
        }

        &.legend {
            text-align: center;

            .tag {
                display: inline-block;
                width: 8px;
                height: 8px;
                padding: 0;
            }
        }

        &.divider {
            height: 40px;

            i {
                margin: 0 !important;
            }
        }
    }

    &.type-2 {

        li {
            overflow: hidden;

            i {
                display: inline-block;
                margin-right: $spacer-x;
                margin-left: 5px;
                font-size: 18px;
                line-height: 40px;
            }

            .title {
                display: inline-block;
                width: auto;
                margin-top: -9px;
                font-size: $font-size-base;
                font-weight: normal;
                line-height: 40px;
                color: $body-color;
            }

            .value {
                float: right;
                font-weight: 600;
            }

            .bars {
                position: absolute;
                top: auto;
                bottom: 0;
                padding: 0;
            }
        }
    }
}

ul.icons-list {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
        position: relative;
        height: 40px;
        vertical-align: middle;

        i {
            display: block;
            float: left;
            width: 35px !important;
            height: 35px !important;
            margin: 2px;
            line-height: 35px !important;
            text-align: center;
        }

        .desc {
            height: 40px;
            margin-left: 50px;
            border-bottom: 1px solid $border-color;

            .title {
                padding: 2px 0 0;
                margin: 0;
            }
            small {
                display: block;
                margin-top: -4px;
                color: $text-muted;
            }
        }

        .value {
            position: absolute;
            top: 2px;
            right: 45px;
            text-align: right;

            strong {
                display: block;
                margin-top: -3px;
            }
        }

        .actions {
            position: absolute;
            top: -4px;
            right: 10px;
            width: 40px;
            height: 40px;
            line-height: 40px;
            text-align: center;

            i {
                float: none;
                width: auto;
                height: auto;
                padding: 0;
                margin: 0;
                line-height: normal;
            }
        }

        &.divider {
            height: 40px;

            i {
                width: auto;
                height: auto;
                margin: 2px 0 0;
                font-size: 18px;
            }
        }
    }
}
