.btn-transparent {
    $color: #fff;
    $background: transparent;
    $border: transparent;

    @include button-variant($color, $background, $border)
}


.btn {
    [class^="icon-"], [class*=" icon-"] {
        display: inline-block;
        margin-top: -2px;
        vertical-align: middle;
    }
}

.btn-facebook,
.btn-twitter,
.btn-linkedin,
.btn-flickr,
.btn-tumblr,
.btn-xing,
.btn-github,
.btn-html5,
.btn-openid,
.btn-stack-overflow,
.btn-youtube,
.btn-css3,
.btn-dribbble,
.btn-google-plus,
.btn-instagram,
.btn-pinterest,
.btn-vk,
.btn-yahoo,
.btn-behance,
.btn-dropbox,
.btn-reddit,
.btn-spotify,
.btn-vine,
.btn-foursquare,
.btn-vimeo {
    position: relative;
    overflow: hidden;
    color: #fff !important;
    text-align: center;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        font-family: 'FontAwesome';
        font-style: normal;
        font-weight: normal;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
    }

    &:hover {
        color: #fff;
    }

    &.icon {

        span {
            display: none;
        }
    }

    &.text {
        &::before {
            display: none;
        }

        span {
            margin-left: 0 !important;
        }
    }
    @include button-social-size($btn-padding-y, $btn-padding-x, $font-size-base, $line-height-base, $btn-border-radius);

    &.btn-lg {
        @include button-social-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-lg, $line-height-lg, $btn-border-radius-lg);
    }

    &.btn-sm {
        @include button-social-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $line-height-sm, $btn-border-radius-sm);
    }

}

.btn-facebook {
    $color: $facebook;

    background: $color;
    &::before {
        content: "\f09a";
        background: darken($color, 5%);

    }

    &:hover {
        background: darken($color, 5%);

        &::before {
            background: darken($color, 10%);
        }
    }
}

